const status = {
  1: "Pending",
  2: "Success",
  3: "Failed",
  4: "Refund",
};

const statusColor = {
  1: "btn btn-warning",
  2: "btn btn-success",
  3: "btn btn-danger",
  4: "btn btn-light",
  5: "btn btn-light",
};

const BankSelect = ({ name, options, ...props }) => {
  const { setFieldValue, setFieldTouched, errors, touched, values } =
    useFormikContext();
  const [field] = useField(name);

  return (
    <Autocomplete
      {...props}
      options={options}
      getOptionLabel={(opt) => opt?.label ?? ""} // ðŸ¡± show bank name
      value={field.value}
      isOptionEqualToValue={(o, v) => o?.value?._id === v?.value?._id}
      onChange={(_, newVal) => {
        setFieldValue(name, newVal); // bank object
        // Autoâ€‘fill / clear IFSC
        setFieldValue("ifsc", newVal?.value?.ifsc_code || "");
        setFieldTouched("ifsc", true, false);
      }}
      renderInput={(params) => (
        <CustomTextField
          {...params}
          label="Select Bank"
          error={touched[name] && Boolean(errors[name])}
          helperText={touched[name] && errors[name]}
          size="small"
        />
      )}
    />
  );
};

const CustomTextField = (props) => (
  <TextField
    variant="outlined"
    size="small"
    InputLabelProps={{
      shrink: true,
      //   style: { fontSize: '16px' },
    }}
    sx={{
      "& label.Mui-focused": {
        color: "var(--bs-nav)",
      },
      "& .MuiOutlinedInput-root": {
        height: 40,
        "& .MuiOutlinedInput-input": {
          py: 0,
        },
        "&:hover fieldset": { borderColor: "var(--bs-primary1)" },
        "&.Mui-focused fieldset": { borderColor: "var(--bs-primary1)" },
      },
    }}
    {...props}
  />
);

const CustomField = ({ name, label, type = "text", ...props }) => (
  <Field name={name}>
    {({ field, form }) => (
      <TextField
        label={label}
        type={type}
        size="small"
        fullWidth
        {...field}
        {...props}
        error={form.touched[name] && Boolean(form.errors[name])}
        helperText={form.touched[name] && form.errors[name]}
        InputLabelProps={{ shrink: true }}
        sx={{
          "& .MuiOutlinedInput-root": {
            height: 40,
            "& .MuiOutlinedInput-input": { py: 0 },
            "&:hover fieldset": { borderColor: "var(--bs-primary1)" },
            "&.Mui-focused fieldset": { borderColor: "var(--bs-primary1)" },
          },
        }}
      />
    )}
  </Field>
);

const CustomBox = ({ children }) => (
  <Box
    sx={{
      width: "100%",
      //   maxWidth: 800,
      mx: "auto",
      //   mt: 5,
      my: 1,
      py: 3,
      px: 3,

      //   bgcolor: '#F8F9FA',
      bgcolor: "white",
      borderRadius: 1,
      boxShadow: 0,
    }}
  >
    {children}
  </Box>
);

const validationSchema = Yup.object({
  mobile: Yup.string()
    .matches(/^[6-9]\d{9}$/, "Invalid mobile number")
    .required("Mobile number is required"),

  accountHolder: Yup.string().required("Account holder name is required"),

  accountNumber: Yup.string()
    .matches(/^\d{9,18}$/, "Invalid account number")
    .required("Account number is required"),

  confirmAccountNumber: Yup.string()
    .oneOf([Yup.ref("accountNumber")], "Account numbers must match")
    .required("Confirm account number is required"),

  // NEW: bank must be chosen (null when nothing selected)
  bank: Yup.object().nullable().required("Select a bank"),

  ifsc: Yup.string()
    .matches(/^[A-Z]{4}0[A-Z0-9]{6}$/, "Invalid IFSC code")
    .required("IFSC code is required"),

  profile: Yup.mixed().required("Bank proof is required"),
});

const AddBankAcount = () => {
  const fileInputRef = useRef();
  const allState = [];
  const [bankList, setBankList] = useState([]);
  const [verficationCharges, setVerificationCharges] = useState("");
  const getVerificationCharges = async () => {
    try {
      let res = await getDMtcharges();
      setVerificationCharges(res?.data?.data?.bankVerificationCharge);
    } catch (error) {}
  };
  useEffect(() => {
    (async () => {
      try {
        const res = await getAllBank(); // <--  [{ bank_name, ifsc_code, â€¦ }]
        const mapped = res?.data?.data?.map((b) => ({
          label: b.bank_name,
          value: b, // keep the whole record in `value`
        }));
        setBankList(mapped);
      } catch (err) {
        console.error("Error fetching bank list:", err);
      }
    })();
  }, []);

  return (
    <Formik
      initialValues={{
        mobile: "",
        accountHolder: "",
        accountNumber: "",
        confirmAccountNumber: "",
        bank: null, // <-- NEW
        ifsc: "",
        profile: null,
      }}
      validationSchema={validationSchema}
      onSubmit={(values) => {
        console.log("Form Data:", values);
        // Submit your form
      }}
    >
      {({
        values,
        setFieldValue,
        errors,
        touched,
        handleChange,
        handleBlur,
      }) => {
        const handleProfileUpload = async (e) => {
          const file = e.target.files[0];
          if (!file) return;
          const allowedTypes = [
            "image/jpeg",
            "image/jpg",
            "image/png",
            "image/webp",
          ];
          if (!allowedTypes.includes(file.type)) {
            alert("Only image files are allowed (jpg, jpeg, png, webp).");
            return;
          }

          const img = new FormData();
          img.append("image", file);

          try {
            const res = await cloudImage(img);
            if (res?.data?.statusCode === "200") {
              setFieldValue("profile", res.data?.data?.url);
            } else {
              setFieldValue("profile", null);
            }
          } catch (error) {
            console.error("Image upload failed:", error);
            setFieldValue("profile", null);
          }
        };
        console.log(values, "values");
        console.log(errors, "errors");
        return (
          <Form>
            <CustomBox>
              <Box display="flex" alignItems="center" gap={2}>
                <IconButton>
                  <ArrowBackIosNewIcon />
                </IconButton>
                <Typography
                  variant="body2"
                  color="var(--bs-nav)"
                  fontWeight="bold"
                  fontSize={18}
                >
                  Add New Bank Account
                </Typography>
              </Box>

              <Box maxWidth={"800px"} m={"auto"} mt={3}>
                <Grid container spacing={3}>
                  <Grid item xs={12} md={6}>
                    <Field
                      name="mobile"
                      label="Mobile Number"
                      fullWidth
                      as={CustomTextField}
                      error={touched.mobile && !!errors.mobile}
                      helperText={touched.mobile && errors.mobile}
                    />
                  </Grid>

                  <Grid item xs={12} md={6}>
                    <Field
                      name="accountHolder"
                      label="A/C Holder Name"
                      fullWidth
                      as={CustomTextField}
                      error={touched.accountHolder && !!errors.accountHolder}
                      helperText={touched.accountHolder && errors.accountHolder}
                    />
                  </Grid>

                  <Grid item xs={12} md={6}>
                    <Field
                      name="accountNumber"
                      label="Account Number"
                      fullWidth
                      as={CustomTextField}
                      error={touched.accountNumber && !!errors.accountNumber}
                      helperText={touched.accountNumber && errors.accountNumber}
                    />
                  </Grid>

                  <Grid item xs={12} md={6}>
                    <Field
                      name="confirmAccountNumber"
                      label="Confirm Account Number"
                      fullWidth
                      as={CustomTextField}
                      error={
                        touched.confirmAccountNumber &&
                        !!errors.confirmAccountNumber
                      }
                      helperText={
                        touched.confirmAccountNumber &&
                        errors.confirmAccountNumber
                      }
                    />
                  </Grid>

                  <Grid item xs={12} md={6}>
                    <Field
                      name="bank"
                      component={BankSelect}
                      options={bankList}
                      fullWidth
                    />
                  </Grid>

                  <Grid item xs={12} md={6}>
                    <Field
                      name="ifsc"
                      label="IFSC Code"
                      fullWidth
                      as={CustomTextField}
                      error={touched.ifsc && !!errors.ifsc}
                      helperText={touched.ifsc && errors.ifsc}
                    />
                  </Grid>

                  <Grid item xs={12} md={12} lg={6}>
                    <Button
                      variant="outlined"
                      fullWidth
                      startIcon={<CloudUploadIcon />}
                      onClick={() => {
                        fileInputRef.current.value = null;
                        fileInputRef.current.click();
                      }}
                      sx={{
                        textTransform: "none",
                        justifyContent: "flex-start",
                        padding: "8px 16px",
                        borderColor: errors.profile ? "error.main" : "grey.400",
                        color: "var(--bs-nav)",
                      }}
                    >
                      Choose File
                    </Button>

                    <input
                      ref={fileInputRef}
                      type="file"
                      accept="image/*"
                      hidden
                      onChange={handleProfileUpload}
                    />

                    {values.profile && (
                      <Box
                        sx={{
                          position: "relative",
                          display: "inline-block",
                          mt: 2,
                        }}
                      >
                        <Avatar
                          src={genrateUrl(values.profile)}
                          alt="Preview"
                          variant="rounded"
                          sx={{ width: 80, height: 80 }}
                        />
                        <IconButton
                          size="small"
                          onClick={() => setFieldValue("profile", null)}
                          sx={{
                            position: "absolute",
                            top: -10,
                            right: -10,
                            bgcolor: "white",
                            border: "1px solid #ccc",
                            boxShadow: 1,
                          }}
                        >
                          <CloseIcon sx={{ fontSize: "10px" }} />
                        </IconButton>
                      </Box>
                    )}

                    {touched.profile && errors.profile && (
                      <Typography
                        color="error"
                        variant="caption"
                        sx={{ mt: 0.5, display: "block" }}
                      >
                        {errors.profile}
                      </Typography>
                    )}
                  </Grid>
                </Grid>

                <Typography
                  color="var(--bs-nav)"
                  sx={{ mt: 2 }}
                  fontWeight={500}
                >
                  Account Verification Charge: â‚¹ {0}
                </Typography>

                <Box
                  sx={{
                    display: "flex",
                    gap: 2,
                    mt: 2,
                    justifyContent: "space-between",
                  }}
                >
                  <Button
                    variant="outlined"
                    color="info"
                    sx={{
                      borderColor: "var(--bs-primary1)",
                      color: "var(--bs-primary1)",
                    }}
                  >
                    <CircularProgress size={24} color="inherit" />
                    {"Account Verification"}
                  </Button>

                  <Button
                    variant="outlined"
                    color="info"
                    sx={{
                      borderColor: "var(--bs-primary1)",
                      color: "var(--bs-primary1)",
                    }}
                    type="submit"
                  >
                    <CircularProgress size={24} color="inherit" />
                    {"Add Beneficiary"}
                  </Button>
                </Box>
              </Box>
            </CustomBox>
          </Form>
        );
      }}
    </Formik>
  );
};

const SettlementOld = () => {
  /* ---------- wallet + dialog state ---------- */
  const [balance, setBalance] = useState(6600); // â‚¹ balance (mock)
  const [open, setOpen] = useState(false); // transfer dialog
  const [amount, setAmount] = useState(""); // text field value
  const [error, setError] = useState(""); // validation message

  /* ---------- transfer history ---------- */
  const [transfers, setTransfers] = useState([]); // [{id, amount, date, status}]
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);

  /* ---------- helpers ---------- */
  const pagedTransfers = useMemo(() => {
    const start = page * rowsPerPage;
    return transfers.slice(start, start + rowsPerPage);
  }, [transfers, page, rowsPerPage]);

  const validate = (val) => {
    if (val === "") return "Amount is required";
    const n = Number(val);
    if (isNaN(n) || n <= 0) return "Enter a positive number";
    if (n > balance) return "Cannot exceed wallet balance";
    if (n > 100000) return "Maximum single transfer is â‚¹1â€¯lakh";
    return "";
  };

  /* ---------- event handlers ---------- */
  const handleOpen = () => {
    setAmount("");
    setError("");
    setOpen(true);
  };

  const handleSubmit = () => {
    const err = validate(amount);
    if (err) {
      setError(err);
      return;
    }
    const amt = Number(amount);
    // 1. Call your API here (POST /transfer { amount: amt })
    // 2. On success, update UI
    setBalance((b) => b - amt);
    setTransfers((prev) => [
      {
        id: `TRF-${Date.now()}`, // unique id
        amount: amt,
        date: new Date().toLocaleDateString(),
        status: "SUCCESS",
      },
      ...prev,
    ]);
    setOpen(false);
  };

  return (
    <>
      <Typography variant="h4" gutterBottom>
        Cashback Settlement
      </Typography>

      {/* Wallet balance + button */}
      <Paper sx={{ p: 3, mb: 4 }}>
        <Box
          display="flex"
          alignItems="center"
          justifyContent="space-between"
          flexWrap="wrap"
          gap={2}
        >
          <Box>
            <Typography variant="subtitle2" color="text.secondary">
              Cashback Wallet Balance
            </Typography>
            <Typography variant="h5" fontWeight={700}>
              â‚¹ {balance.toLocaleString("en-IN")}
            </Typography>
          </Box>

          <Button variant="contained" size="large" onClick={handleOpen}>
            Transfer to Bank
          </Button>
        </Box>
      </Paper>

      {/* Transfer history */}
      <Paper>
        <TableContainer>
          <Table size="small">
            <TableHead>
              <TableRow>
                <TableCell>TxnÂ ID</TableCell>
                <TableCell align="right">AmountÂ (â‚¹)</TableCell>
                <TableCell>Date</TableCell>
                <TableCell>Status</TableCell>
              </TableRow>
            </TableHead>

            <TableBody>
              {pagedTransfers.map((row) => (
                <TableRow key={row.id}>
                  <TableCell>{row.id}</TableCell>
                  <TableCell align="right">{row.amount}</TableCell>
                  <TableCell>{row.date}</TableCell>
                  <TableCell>{row.status}</TableCell>
                </TableRow>
              ))}

              {pagedTransfers.length === 0 && (
                <TableRow>
                  <TableCell colSpan={4} align="center">
                    No transfers yet
                  </TableCell>
                </TableRow>
              )}
            </TableBody>
          </Table>
        </TableContainer>

        <TablePagination
          component="div"
          page={page}
          count={transfers.length}
          onPageChange={(_, p) => setPage(p)}
          rowsPerPage={rowsPerPage}
          onRowsPerPageChange={(e) => {
            setRowsPerPage(parseInt(e.target.value, 10));
            setPage(0);
          }}
        />
      </Paper>

      {/* Transfer dialog */}
      <Dialog
        open={open}
        onClose={() => setOpen(false)}
        maxWidth="xs"
        fullWidth
      >
        <DialogTitle>Transfer to Bank</DialogTitle>

        <DialogContent dividers>
          <TextField
            label="Amount (â‚¹)"
            fullWidth
            value={amount}
            onChange={(e) => {
              setAmount(e.target.value);
              if (error) setError(validate(e.target.value));
            }}
            type="number"
            inputProps={{ min: 1 }}
            error={Boolean(error)}
            helperText={error}
          />
          {/* Extra inputs (bank, remarks...) can go here */}
        </DialogContent>

        <DialogActions>
          <Button onClick={() => setOpen(false)}>Cancel</Button>
          <Button variant="contained" onClick={handleSubmit}>
            Transfer
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
};

const SettlementPopup = ({ setResetKey }) => {
  const { popupOpen, setpopupOpen, walletData, setLoader, walletCall } =
    useCashBackProvider();
  const [cashbackBankList, setCashBackBankList] = useState([]);
  const [step, setStep] = useState(1);
  const [values, setValues] = useState({
    bank: null,
    amount: "",
    tpin: "",
  });
  const [errors, setErrors] = useState({});

  const validateStep1 = () => {
    const next = {};
    if (!values.bank) next.bank = "Select a bank";

    const amt = Number(values.amount);
    if (!values.amount) next.amount = "Enter an amount";
    else if (Number.isNaN(amt) || amt <= 99)
      next.amount = "Minimum Amount is 100.";
    else if (amt > (walletData?.cashback_wallet || 0))
      next.amount = "Amount exceeds wallet balance";

    setErrors(next);
    return Object.keys(next).length === 0;
  };

  const validateStep2 = () => {
    if (!values.tpin) return "Enter TPIN";
    if (!/^\d{4}$/.test(values.tpin)) return "TPIN must be 4 digits";
    return "";
  };

  const handleChange = (field) => (e, newValue) => {
    const value = field === "bank" ? newValue : e.target.value;
    setValues((p) => ({ ...p, [field]: value }));
    if (errors[field]) setErrors((e) => ({ ...e, [field]: "" }));
  };

  /* ---------- SUBMITS ---------- */
  const handleNext = () => {
    if (validateStep1()) setStep(2);
  };

  const handleConfirm = withLocation(async (latitude, longitude) => {
    const err = validateStep2();
    if (err) {
      setErrors((e) => ({ ...e, tpin: err }));
      return;
    }

    let body = {
      bank_id: values?.bank?._id,
      latlong: `${latitude},${longitude}`,
      txnMode: "imps",
      tpin: values?.tpin,
      amount: values?.amount,
    };

    setLoader(true);
    try {
      let res = await cashbackSettlement(body);
      toastMessage(res?.data?.error, res?.data?.message);
      if (res?.data?.statusCode === 200) {
        setpopupOpen(false);
        setResetKey((pre) => !pre);
        setStep(1);
        setValues({ bank: null, amount: "", tpin: "" });
        walletCall();
      }
      if (res?.data?.statusCode === 405) {
        setValues((pre) => ({ ...pre, tpin: "" }));
        // toastMessage(res?.data?.error , res?.data?.message)
        return;
      }

      //  // remove this block after this
      //  toastMessage(false , 'Fund Transfer Succssfully.')
      //  setpopupOpen(false);
      //  setResetKey((pre)=>!pre)
      //  setStep(1);
      //  setValues({ bank: null, amount: "", tpin: "" });
      //  walletCall()
    } catch (error) {
    } finally {
      setLoader(false);
    }
  });

  // const closeAll = () => {
  //   setpopupOpen(false);
  //   setStep(1);
  //   setValues({ bank: null, amount: "", tpin: "" });
  //   setErrors({});
  // };

  useEffect(() => {
    (async () => {
      try {
        const res = await bankListForCashbackSettlement();
        setCashBackBankList(res?.data?.data || []);
      } catch (err) {
        console.error("Bank list fetch failed:", err);
      }
    })();
  }, []);

  return (
    <>
      <Dialog
        open={popupOpen}
        onClose={() => setpopupOpen(false)}
        PaperProps={{
          sx: () => ({
            borderRadius: 4,
            paddingY: 2,
            backdropFilter: "blur(10px)",
            background: "white",
            boxShadow: "0 8px 32px rgba(0, 0, 0, 0.2)",
            width: "100%",
            maxWidth: 450,
            //  overflow: tabs?.length === 0 ? 'hidden' : 'auto',
          }),
        }}
      >
        <AnimatePresence mode="wait">
          {step === 1 ? (
            <motion.div
              key="step1"
              initial={{ opacity: 0, x: -30 }}
              animate={{ opacity: 1, x: 0 }}
              exit={{ opacity: 0, x: 30 }}
              transition={{ duration: 0.3 }}
            >
              <DialogTitle
                sx={{
                  color: "var(--bs-primary2)",
                  fontWeight: 600,
                  textAlign: "center",
                  fontSize: "1.0rem",
                }}
              >
                Transfer Money into Bank Account
                <br />
                Your Cashback Wallet Balance is : {walletData?.cashback_wallet}
              </DialogTitle>
              <Box
                display="flex"
                justifyContent="center"
                alignItems="center"
                flexDirection={"column"}
                mb={2}
                gap={1}
                sx={{ px: 2 }}
              >
                <Box sx={{ maxWidth: 380, width: "100%", mb: 2 }}>
                  <Autocomplete
                    options={cashbackBankList}
                    getOptionLabel={(option) => option?.bank_name || ""}
                    value={values.bank}
                    onChange={handleChange("bank")}
                    size="small"
                    renderOption={(props, option) => (
                      <li {...props}>
                        <Box sx={{ fontSize: "13px", color: "var(--bs-nav)" }}>
                          {option.bank_name}
                        </Box>
                      </li>
                    )}
                    renderInput={(params) => (
                      <CustomTextField
                        {...params}
                        label="Select Bank"
                        error={Boolean(errors.bank)}
                        helperText={errors.bank}
                        InputLabelProps={{
                          sx: {
                            "&.MuiInputLabel-root:not(.MuiInputLabel-shrink)": {
                              top: "-3px",
                            },
                          },
                        }}
                      />
                    )}
                  />
                </Box>
                {values?.bank?.bank_account_number && (
                  <>
                    <Box sx={{ maxWidth: 380, width: "100%", mb: 2 }}>
                      <CustomTextField
                        label="Account Holder Name"
                        name="account holdernamer"
                        type="text"
                        disabled={true}
                        value={values?.bank?.bank_account_name || ""}
                        fullWidth
                      />
                    </Box>
                    <Box sx={{ maxWidth: 380, width: "100%", mb: 2 }}>
                      <CustomTextField
                        label="IFSC code"
                        name="IFSC code"
                        type="text"
                        disabled={true}
                        value={values?.bank?.bank_ifsc || ""}
                        fullWidth
                      />
                    </Box>
                    <Box sx={{ maxWidth: 380, width: "100%", mb: 2 }}>
                      <CustomTextField
                        label="Account Number"
                        name="account number"
                        type="number"
                        disabled={true}
                        value={values?.bank?.bank_account_number || ""}
                        fullWidth
                      />
                    </Box>
                  </>
                )}
                <Box sx={{ maxWidth: 380, width: "100%" }}>
                  <CustomTextField
                    label="Amount"
                    name="amt"
                    type="number"
                    placeholder="Enter Amount"
                    value={values.amount}
                    onChange={handleChange("amount")}
                    error={Boolean(errors.amount)}
                    helperText={errors.amount}
                    autoComplete="off"
                    inputProps={{ inputMode: "numeric", maxLength: 10 }}
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          <CurrencyRupeeIcon />
                        </InputAdornment>
                      ),
                    }}
                    fullWidth
                  />
                </Box>
              </Box>
              <DialogActions
                sx={{ justifyContent: "center", mt: 2, columnGap: 2 }}
              >
                <Box
                  sx={{
                    display: "flex",
                    width: "100%",
                    maxWidth: 380,
                    justifyContent: "space-between",
                  }}
                >
                  <Box>
                    <Button
                      onClick={() => setpopupOpen(false)}
                      color="error"
                      variant="outlined"
                      sx={{
                        borderRadius: 2,
                        px: 4,
                        width: "150px",
                      }}
                    >
                      Cancel
                    </Button>
                  </Box>

                  <Box>
                    <Button
                      onClick={handleNext}
                      color="primary"
                      variant="contained"
                      sx={{
                        backgroundColor: "var(--bs-primary2)",
                        borderRadius: 2,
                        px: 4,
                        width: "150px",
                      }}
                    >
                      Continue
                    </Button>
                  </Box>
                </Box>
              </DialogActions>
            </motion.div>
          ) : (
            <motion.div
              key="step2"
              initial={{ opacity: 0, x: 30 }}
              animate={{ opacity: 1, x: 0 }}
              exit={{ opacity: 0, x: -30 }}
              transition={{ duration: 0.3 }}
            >
              <DialogTitle
                sx={{
                  color: "var(--bs-primary1)",
                  fontWeight: 600,
                  textAlign: "center",
                  fontSize: "1.3rem",
                }}
              >
                Confirm Transfer
              </DialogTitle>
              <Box
                display="flex"
                flexDirection="column"
                alignItems="center"
                gap={3}
                px={2}
                pb={2}
              >
                <Typography fontSize="0.95rem" textAlign="center">
                  Are you sure you want to transfer&nbsp;
                  <strong>â‚¹{values.amount}</strong> to&nbsp;
                  <p>
                    <strong>{values?.bank?.bank_name}</strong> &nbsp;
                    <strong>{values?.bank?.bank_account_number}</strong>{" "}
                  </p>
                </Typography>
                <Box sx={{ maxWidth: 380, width: "100%" }}>
                  <CustomTextField
                    label="TPIN"
                    type="password"
                    name="tpin"
                    placeholder="Enter TPin"
                    value={values.tpin}
                    onChange={handleChange("tpin")}
                    error={Boolean(errors.tpin)}
                    helperText={errors.tpin}
                    autoComplete="new-password"
                    inputProps={{ inputMode: "numeric", maxLength: 4 }}
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          <LockIcon />
                        </InputAdornment>
                      ),
                    }}
                    // sx={{ mt: 2 }}
                    fullWidth
                  />
                </Box>
              </Box>
              <DialogActions
                sx={{ justifyContent: "center", mt: 2, columnGap: 2 }}
              >
                <Box
                  sx={{
                    display: "flex",
                    width: "100%",
                    maxWidth: 380,
                    justifyContent: "space-between",
                  }}
                >
                  <Box>
                    <Button
                      onClick={() => {
                        setStep(1);
                        setValues((pre) => ({ ...pre, tpin: "" }));
                      }}
                      color="error"
                      variant="outlined"
                      sx={{
                        borderRadius: 2,
                        px: 4,
                        width: "150px",
                      }}
                    >
                      Back
                    </Button>
                  </Box>

                  <Box>
                    <Button
                      onClick={handleConfirm}
                      color="primary"
                      variant="contained"
                      sx={{
                        backgroundColor: "var(--bs-primary1)",
                        borderRadius: 2,
                        px: 4,
                        width: "150px",
                      }}
                    >
                      Confirm
                    </Button>
                  </Box>
                </Box>
              </DialogActions>
            </motion.div>
          )}
        </AnimatePresence>
      </Dialog>
    </>
  );
};

const Settlement = () => {
  const today = new Date().toISOString().split("T")[0];
  const { popupOpen, setpopupOpen, loader, setLoader } = useCashBackProvider();

  const [page, setPage] = useState(1);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [resetKey, setResetKey] = useState(false);
  const [totalData, setTotalData] = useState(0);
  const [data, setData] = useState([]);

  const [filters, setFilters] = useState({
    startDate: today,
    endDate: today,
    status: "",
    search: "",
  });

  const handleChangePage = (_, value) => {
    setPage(value);
  };
  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(Number(event.target.value));
    setPage(1); // reset to first page when rowsPerPage changes
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFilters((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleSubmit = () => {
    setResetKey((pre) => !pre);
    // you can pass `filters` to your API or logic here
  };

  const handleReset = () => {
    setFilters({
      startDate: "",
      endDate: "",
      status: "",
      search: "",
    });

    setPage(1);
    setRowsPerPage(10);
    setResetKey((pre) => !pre);
  };

  const getList = async () => {
    let body = {
      page: page,
      count: rowsPerPage,
      start_date: filters.startDate,
      end_date: filters.endDate,
      status: filters.status,
      search: filters.search,
    };
    try {
      let res = await cashbackSettlementReport(body);
      if (res?.data?.statusCode === 200) {
        setData(res?.data?.data?.trans);
        setTotalData(res?.data?.data?.total);
      } else {
        setData([]);
        setTotalData(0);
      }
    } catch (error) {}
  };

  useEffect(() => {
    getList();
  }, [resetKey, page, rowsPerPage]);

  const statusEnquiry = async (id) => {
    setLoader(true);
    try {
      let res = await cashbackSettlementEnquiry(id);

      setResetKey((pre) => !pre);
      toastMessage(res?.data?.error, res?.data?.message);
    } catch (error) {
    } finally {
      setLoader(false);
    }
  };

  const totalPages = Math.ceil(totalData / rowsPerPage);
  return (
    <>
      <div className="PageHeading">
        <h1>Cashback Settlement</h1>
      </div>

      <CustomBox>
        <Box justifyContent={"flex-end"} display={"flex"}>
          <Button
            variant="contained"
            color="primary"
            onClick={() => setpopupOpen(true)}
            sx={{
              marginBottom: 2,
              borderColor: "var(--bs-primary1)", // Custom colors
              width: "200px",
              backgroundColor: "var(--bs-primary2)", // Custom colors
              color: "white",
            }}
          >
            Make Settlement
          </Button>
        </Box>

        <div className="card">
          <div className="card-header"> {/* <span>Filter</span> */}</div>
          <div className="card-body">
            <form>
              <div className="form-row" style={{ alignItems: "end" }}>
                <div className="form-group col-md-4">
                  <label>Start Date</label>
                  <input
                    type="date"
                    name="startDate"
                    className="form-control"
                    value={filters.startDate}
                    onChange={handleChange}
                    max={today}
                  />
                </div>
                <div className="form-group col-md-4">
                  <label>End Date</label>
                  <input
                    type="date"
                    name="endDate"
                    className="form-control"
                    value={filters.endDate}
                    onChange={handleChange}
                    max={today}
                  />
                </div>

                <div className="form-group col-md-4">
                  <label>Status</label>
                  <FormControl fullWidth size="small">
                    <Select
                      value={filters?.status || ""}
                      name="status"
                      onChange={handleChange}
                      displayEmpty
                      sx={{
                        color: "rgba(0, 0, 0, 0.6)",
                        fontSize: "14px",
                        "& .MuiOutlinedInput-notchedOutline": {
                          borderColor: "grey.400",
                        },

                        "&:hover .MuiOutlinedInput-notchedOutline": {
                          borderColor: "grey.400",
                        },
                        "&.Mui-focused .MuiOutlinedInput-notchedOutline": {
                          borderColor: "grey.400",
                        },
                      }}
                    >
                      <MenuItem
                        value=""
                        disabled
                        sx={{
                          fontSize: "14px",
                          color: "#000000",
                          px: 2,
                        }}
                      >
                        Select Status
                      </MenuItem>
                      {Object?.entries(status)?.map(([key, value]) => (
                        <MenuItem
                          value={key}
                          sx={{
                            fontSize: "14px",
                            color: "#000000",
                            px: 2,
                          }}
                        >
                          {value}
                        </MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                </div>
                <div className="form-group col-md-4">
                  <label>Txn Id / Account Number / Mobile No</label>
                  <input
                    type="text"
                    name="search"
                    className="form-control"
                    value={filters.search}
                    onChange={handleChange}
                    placeholder="Txn Id / Account Number / Mobile No"
                  />
                </div>

                <div className="form-group col-md-12 col-lg-12">
                  <div>
                    <button
                      type="button"
                      className="btn btn-secondary mr-3"
                      onClick={handleSubmit}
                    >
                      Search
                    </button>
                    <button
                      type="button"
                      className="btn btn-light"
                      onClick={handleReset}
                    >
                      Reset
                    </button>
                  </div>
                </div>
              </div>
            </form>
          </div>
        </div>

        <Box sx={{ my: 2 }}>
          <TableContainer component={Box} elevation={3}>
            <Table style={{ whiteSpace: "nowrap" }}>
              <TableHead sx={{ backgroundColor: "#f5f5f5" }}>
                <TableRow>
                  <TableCell>
                    <strong>S.No.</strong>
                  </TableCell>
                  <TableCell style={{ minWidth: "180px" }}>
                    <strong> Date</strong>
                  </TableCell>
                  <TableCell style={{ minWidth: "180px" }}>
                    <strong>Transaction Id</strong>
                  </TableCell>
                  <TableCell style={{ minWidth: "180px" }}>
                    <strong>UTR Number</strong>
                  </TableCell>
                  <TableCell style={{ minWidth: "180px" }}>
                    <strong>Bank Name</strong>
                  </TableCell>
                  <TableCell style={{ minWidth: "120px" }}>
                    <strong>Account Number</strong>
                  </TableCell>
                  <TableCell style={{ minWidth: "100px" }}>
                    <strong>Opening Balance</strong>
                  </TableCell>
                  <TableCell style={{ minWidth: "60px" }}>
                    <strong>Charges</strong>
                  </TableCell>
                  <TableCell style={{ minWidth: "100px" }}>
                    <strong>Amount</strong>
                  </TableCell>
                  <TableCell style={{ minWidth: "120px" }}>
                    <strong>Closing Balance</strong>
                  </TableCell>

                  {/* <TableCell><strong>Qty</strong></TableCell> */}
                  {/* <TableCell><strong>Address</strong></TableCell> */}
                  <TableCell style={{ minWidth: "180px" }}>
                    <strong>Status</strong>
                  </TableCell>
                  <TableCell style={{ minWidth: "180px" }}>
                    <strong>Enquiry</strong>
                  </TableCell>
                </TableRow>
              </TableHead>

              <TableBody>
                {data?.map((item, index) => (
                  <TableRow key={index} hover>
                    <TableCell sx={{ color: "var(--bs-nav)" }}>
                      {index + 1 + (page - 1) * rowsPerPage}
                    </TableCell>
                    <TableCell sx={{ color: "var(--bs-nav)" }}>
                      {item?.createdAt}{" "}
                    </TableCell>
                    <TableCell sx={{ color: "var(--bs-nav)" }}>
                      {item?.txn_id}{" "}
                    </TableCell>

                    <TableCell sx={{ color: "var(--bs-nav)" }}>
                      {item?.utr || "Not generated"}
                    </TableCell>
                    <TableCell sx={{ color: "var(--bs-nav)" }}>
                      {item?.bank_name}
                    </TableCell>
                    <TableCell sx={{ color: "var(--bs-nav)" }}>
                      {item?.account_number}
                    </TableCell>
                    <TableCell sx={{ color: "var(--bs-nav)" }}>
                      {item?.o_bal}
                    </TableCell>
                    <TableCell sx={{ color: "var(--bs-nav)" }}>
                      {item?.charge}
                    </TableCell>
                    <TableCell sx={{ color: "var(--bs-nav)" }}>
                      {item?.amount}
                    </TableCell>
                    <TableCell sx={{ color: "var(--bs-nav)" }}>
                      {item?.c_bal}
                    </TableCell>

                    <TableCell sx={{ color: "var(--bs-nav)" }}>
                      <button
                        className={`btn ${statusColor[item.status]}`}
                        style={{ width: "130px" }}
                      >
                        {status[item?.status] || "unknown"}
                      </button>
                    </TableCell>
                    <TableCell sx={{ color: "var(--bs-nav)" }}>
                      <Button
                        variant="contained"
                        color="primary"
                        disabled={loader || item?.status !== 1}
                        onClick={() => statusEnquiry(item?._id)}
                        sx={{
                          borderColor: "var(--bs-primary1)", // Custom colors
                          width: "150px",
                          backgroundColor: "var(--bs-primary1)", // Custom colors
                          color: "white",
                        }}
                      >
                        Enquiry
                      </Button>
                    </TableCell>
                  </TableRow>
                ))}

                {data?.length === 0 && (
                  <TableRow>
                    <TableCell
                      colSpan={8}
                      align="center"
                      sx={{ color: "gray" }}
                    >
                      No data found
                    </TableCell>
                  </TableRow>
                )}
              </TableBody>
            </Table>
          </TableContainer>
        </Box>
        <Box
          display="flex"
          justifyContent="space-between"
          alignItems="center"
          mt={2}
          flexWrap="wrap"
          gap={2}
        >
          <Box display="flex" alignItems="center" gap={2}>
            <FormControl size="small" sx={{ width: "150px" }}>
              <InputLabel>Rows per page</InputLabel>
              <Select
                value={rowsPerPage}
                onChange={handleChangeRowsPerPage}
                label="Rows per page"
                sx={{
                  fontSize: "14px",
                  "& .MuiOutlinedInput-notchedOutline": {
                    borderColor: "#ccc",
                  },
                  "&:hover .MuiOutlinedInput-notchedOutline": {
                    borderColor: "var(--bs-primary1)",
                  },
                  "&.Mui-focused .MuiOutlinedInput-notchedOutline": {
                    borderColor: "var(--bs-primary1)",
                  },
                }}
              >
                {[5, 10, 25, 50].map((num) => (
                  <MenuItem key={num} value={num}>
                    {num}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>

            {/* Total Entries Count */}
            <Typography
              variant="body2"
              sx={{ fontSize: "14px", color: "#555" }}
            >
              Total Entries: {totalData}
            </Typography>
          </Box>

          <Pagination
            count={totalPages}
            page={page}
            onChange={handleChangePage}
            // color="primary"
            shape="rounded"
            showFirstButton
            showLastButton
            sx={{
              "& .MuiPaginationItem-root": {
                color: "var(--bs-primary1)",
                borderRadius: "8px",
              },
              "& .MuiPaginationItem-root.Mui-selected": {
                backgroundColor: "var(--bs-primary1)",
                color: "#fff",
                fontWeight: "bold",
                "&:hover": {
                  backgroundColor: "var(--bs-primary1)",
                },
              },
            }}
          />
        </Box>
      </CustomBox>

      {popupOpen && <SettlementPopup setResetKey={setResetKey} />}
    </>
  );
};

const CashbackPayout = () => {
  const { currentPage, loader } = useCashBackProvider();

  return (
    <>
      {loader && <Loader />}

      {/* {currentPage ==='' && <AddBankAcount/>} */}

      {currentPage === "" && <Settlement />}
    </>
  );
};

export default CashbackPayout;
